import {
  FormFieldHooksParams,
  Frequencies,
  InternalAccountFormItem,
  internalTransferScheduleFreqOnInitHook,
} from '@backbase/payment-orders-ang';
import { Subscription } from 'rxjs';
import { pairwise, startWith, tap } from 'rxjs/operators';
import { AbstractControl } from '@angular/forms';
import { Loan, AccountRole } from '@backbase/loans-collection-ui-ang';
import { changeFields } from './loans-payment-config';

const subscriptions: Subscription[] = [];
let isEditMode: boolean | undefined = false;
let amountOptionsSubscription: Subscription | undefined;
let frequencySelectSubscription: Subscription | undefined;

const getAccountForm = (group: AbstractControl, name: string) => group?.parent?.get(name);
let selectedLoan: Loan | undefined;
export const scheduleFrequencyForAmountOption = [
  { label: 'Once', value: Frequencies.ONCE },
  { label: 'Daily', value: Frequencies.DAILY },
  { label: 'Weekly', value: Frequencies.WEEKLY },
  { label: 'Biweekly', value: Frequencies.BIWEEKLY },
  { label: 'Monthly', value: Frequencies.MONTHLY },
  { label: 'Quarterly', value: Frequencies.QUARTERLY },
  { label: 'Annually', value: Frequencies.YEARLY },
];

export const setLoan = (loan: Loan | undefined) => {
  selectedLoan = loan;
  if (loan) {
    changeFields(loan);
  }
};

export const frequencySelectInitHook = (params: FormFieldHooksParams): void => {
  internalTransferScheduleFreqOnInitHook(params);

  frequencySelectSubscription = params.control.valueChanges.subscribe(() => {
    params.component.toggleDependants();
  });
};

export const frequencySelectDestroyHook = (): void => {
  frequencySelectSubscription?.unsubscribe();
};

export const amountCurrencyGroupInitHook = (params: FormFieldHooksParams) => {
  amountInputCounterPartyInitHook(params);

  const remittanceInfoControl = params.group;
  const amountGroup = remittanceInfoControl?.get('amountGroup');
  const scheduleFrequency = remittanceInfoControl.get('scheduleFrequency');

  amountOptionsSubscription = remittanceInfoControl
    ?.get('amountOptions')
    ?.valueChanges.pipe(startWith(remittanceInfoControl.value), pairwise())
    .subscribe(([prev, next]: [number, number]) => {
      if (prev !== next) {
        if (next === 1) {
          amountGroup?.patchValue({
            currency: selectedLoan?.currencyCode,
            amount: cutAmount(selectedLoan?.inArrearsAmount),
          });
          scheduleFrequency?.patchValue(scheduleFrequencyForAmountOption);
        }
        if (next === 0) {
          scheduleFrequency?.patchValue(scheduleFrequencyForAmountOption);
        }
      }
    });
};

export const amountCurrencyGroupDestroyHook = () => {
  amountOptionsSubscription?.unsubscribe();
};

export const internalAmountOptionsInitHook = (params: FormFieldHooksParams): void => {
  isEditMode = params.component.stateService.paymentStateSubject.value === 'EDIT';
};

export const amountInputInitHook = (form: FormFieldHooksParams): void => {
  const initiatorControl = getAccountForm(form.group, 'initiator');
  initiatorControl?.valueChanges.subscribe((data: { currency: string }) => {
    form.component.options.currencies = [data.currency];
  });
};

export const amountInputCounterPartyInitHook = ({ component, group }: FormFieldHooksParams): void => {
  const amountGroup = group?.get('amountGroup');

  if (selectedLoan?.inArrearsAmount && !amountGroup?.value.amount) {
    amountGroup?.patchValue({
      currency: selectedLoan?.currencyCode,
      amount: cutAmount(selectedLoan.inArrearsAmount),
    });
  }

  const counterpartyControl = getAccountForm(group, 'counterparty');
  counterpartyControl?.valueChanges.subscribe((data: { currency: string }) => {
    component.options.currencies = [data.currency];
  });
};

export const counterPartyPaymentInitHook = ({ component, group }: FormFieldHooksParams): void => {
  component.options.mapItems = (accounts: InternalAccountFormItem[]): InternalAccountFormItem[] =>
    !!selectedLoan?.id
      ? accounts.filter((account: InternalAccountFormItem) => {
          if (account.id === selectedLoan?.id) {
            component.selectItem(account);

            if (account.defaultRecord) {
              const defaultRecord = account.defaultRecord as any;
              defaultRecord.nextRepaymentAmount = selectedLoan?.nextRepaymentAmount;
              defaultRecord.inArrearsAmount = cutAmount(selectedLoan.inArrearsAmount);
            }
          }
          return account.id === selectedLoan?.id;
        })
      : accounts;

  const remittanceInfoControl = getAccountForm(group, 'remittanceInfo');

  const changesSubscription = group.valueChanges.subscribe((value) => {
    if (value.productKindName === 'Loan') {
      group.patchValue({ role: AccountRole.DEBTOR }, { onlySelf: true, emitEvent: false });
    } else {
      group.patchValue({ role: AccountRole.CREDITOR }, { onlySelf: true, emitEvent: false });
    }

    const amountOptions = remittanceInfoControl?.get('amountOptions');

    if (amountOptions?.value !== 0 && amountOptions?.value !== 1 && !isEditMode) {
      if (
        (!selectedLoan?.inArrearsAmount || selectedLoan?.inArrearsAmount === '0') &&
        !!selectedLoan?.nextRepaymentAmount
      ) {
        amountOptions?.setValue(0);
      }

      if (
        !!selectedLoan?.inArrearsAmount &&
        selectedLoan?.inArrearsAmount !== '0' &&
        !!selectedLoan?.nextRepaymentAmount
      ) {
        amountOptions?.setValue(1);
      }
    }
  });

  subscriptions.push(changesSubscription);
};

export const initiatorAdvanceInitHook = ({ component }: FormFieldHooksParams): void => {
  const selectedId = selectedLoan?.id;
  component.options.mapItems = (accounts: InternalAccountFormItem[]): InternalAccountFormItem[] =>
    !!selectedId
      ? accounts.filter((account: InternalAccountFormItem) => {
          if (account.id === selectedId) {
            component.selectItem(account);
          }

          return account.id === selectedId;
        })
      : accounts;
};

export const counterpartyInitAdvanceHook = (params: FormFieldHooksParams): void => {
  params.component.options.mapItems = (accounts: InternalAccountFormItem[]): InternalAccountFormItem[] => {
    const currency = selectedLoan?.currencyCode;

    return !!currency ? accounts.filter((account: InternalAccountFormItem) => account.currency === currency) : accounts;
  };

  selectAccountDefaultSettlement(params);
};

export const initiatorInitHook = ({ component }: FormFieldHooksParams): void => {
  component.options.mapItems = (accounts: InternalAccountFormItem[]): InternalAccountFormItem[] =>
    !!selectedLoan?.id
      ? accounts.filter((account: InternalAccountFormItem) => {
          if (
            account.id === selectedLoan?.defaultSettlementAccountId &&
            account.currency === selectedLoan?.currencyCode
          ) {
            component.selectItem(account);
          }
          return account.currency === selectedLoan?.currencyCode;
        })
      : accounts;
};

const selectAccountDefaultSettlement = ({ component }: FormFieldHooksParams): void => {
  const defaultSettlementId = selectedLoan?.defaultSettlementAccountId;
  if (defaultSettlementId) {
    const subscription = component.options.items$
      .pipe(
        tap((accounts: InternalAccountFormItem[]): void => {
          const account = accounts.find(
            (internalAccount: InternalAccountFormItem) => internalAccount.id === defaultSettlementId,
          );
          component.selectItem(account);
        }),
      )
      .subscribe();
    subscriptions.push(subscription);
  }
};

export const destroyHook = (): void => {
  subscriptions.forEach((subscription: Subscription) => subscription.unsubscribe());
};

const cutAmount = (val?: string) => (val ? val.split('.')[0] + '.' + val.split('.')[1].slice(0, 2) : '');
